/* EmployeeForm.css */

.form-container {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background-color: #f4f4f4;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

h1 {
  text-align: center;
  color: #333;
}

form label {
  display: block;
  margin-top: 10px;
  font-weight: bold;
  color: #555;
}

form input,
form textarea {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  color: #333;
  transition: border-color 0.3s, background-color 0.3s;
}

form input:focus,
form textarea:focus {
  border-color: #007bff;
  background-color: #f9f9f9;
  outline: none;
}

form button {
  display: block;
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}

form button:hover {
  background-color: #0056b3;
}

.form-error {
  color: red;
  font-size: 0.9rem;
  margin-top: 5px;
}

.form-success {
  color: green;
  font-size: 0.9rem;
  margin-top: 5px;
}
